import * as React from 'react';
import styles from './Spotlight.module.scss';
import { ISpotlightProps } from './ISpotlightProps';
import { escape } from '@microsoft/sp-lodash-subset';
import { ISpotlightState } from './ISpotlightState';
import AllSpotlight from './AllSpotlight';
import SpotlightID from './SpotlightID';
import common from '../../common/common';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { Link } from 'office-ui-fabric-react/lib/components/Link';
import { UrlQueryParameterCollection } from '@microsoft/sp-core-library';

export default class Spotlight extends React.Component<ISpotlightProps, ISpotlightState> {

  constructor(props: ISpotlightProps, state: ISpotlightState) {
    super(props);
    //Initialize the State
    this.state = {
      isLoading: true,
      items: [
        {
          EmployeeName: {
            EMail: '',
            FirstName: '',
            LastName: '',
            JobTitle: ''
          },
          MediaHouse: 'No SpotLights to Display',
          ID:0
        }
      ]
    };
  }

  public async componentDidMount() {
    await this.getCurrentUserLocation();
  }

  public componentWillReceiveProps(nextProps): void {
    this.getCurrentUserLocation();
  }

  //get user's Current location 
  public async getCurrentUserLocation() {
    await this.props.graphClient.api('/me')  //get current user's profile
      .get((error: any, userResponse: any, rawResponse?: any) => {
        //# TODO change Department to serviceline and officelocation to City
        this.props.graphClient.api('/me/department')  //get current user's department
          .get((ServiceLineerror: any, sericeLine: any, ServiceLinerawResponse?: any) => {
            this.getSpotlightRecords(userResponse.officeLocation, sericeLine.value);
          });
      });
  }
  //get StarPerformer's Records
  private getSpotlightRecords(location, department): void {
    var method = 'get items for Spotlight';
    let newCommonObj: common = new common();
    var query;
    if (this.props.serviceline == undefined)  //serviceline not selected query is fetch alerts acording to current user's sericeline and location
      query = "?$select=*,EmployeeName/JobTitle,EmployeeName/EMail,EmployeeName/FirstName,EmployeeName/LastName,EmployeeName/EMail,TaxCatchAll/Term&$filter=TaxCatchAll/Term eq '" + department + "' and TaxCatchAll/Term eq '" + location + "'&$expand=TaxCatchAll/Label,EmployeeName/Id&$orderby=PublishDate desc&$top=5";
    else if (this.props.serviceline == "ALL SERVICELINE") //serviceline not selected query is fetch alerts acording to all sericeline and user's location
     // query = "?$filter=TaxCatchAll/Term eq '" + location + "'&$orderby=PublishDate desc&$top=5";
      query = "?$select=*,EmployeeName/JobTitle,EmployeeName/EMail,EmployeeName/FirstName,EmployeeName/LastName,EmployeeName/EMail&$expand=EmployeeName/Id&$orderby=PublishDate desc&$Filter=(TaxCatchAll/Term eq '" + location + "')&$top=5";
    else//serviceline not selected query is fetch alerts acording to selected sericeline and user's location
      query = "?$select=*,EmployeeName/JobTitle,EmployeeName/EMail,EmployeeName/FirstName,EmployeeName/LastName,EmployeeName/EMail,TaxCatchAll/Term&$filter=TaxCatchAll/Term eq '" + this.props.serviceline + "' and TaxCatchAll/Term eq '" + location + "'&$expand=TaxCatchAll/Label,EmployeeName/Id&$orderby=PublishDate desc&$top=5";

    newCommonObj.getDataFromListUsingGuid(this.props.siteUrl, this.props.listId, query, method).then(async (res) => {
      if (res.data.value.length > 0) {
        //if service line is all then ordring First Your serviceline display after that other
        if (this.props.serviceline == "ALL SERVICELINE") {
          var dataFiltered = res.data.value.filter(
            data => data.ServiceArea[0].Label == department //Filter by your service line
          );
          if (dataFiltered.length > 0) {
            if (dataFiltered.length == res.data.value.length) {//If all filtered data's service line = yur service line
              this.setState({ items: dataFiltered, isLoading: false });
            }
            else {
              var i = 0;
              for (i = 0; i < dataFiltered.length; i++) {
                const index = res.data.value.indexOf(dataFiltered[i]);
                if (index > -1) {
                  res.data.value.splice(index, 1);
                }
              }
              let orderservice = [];
              dataFiltered.forEach(myservice => {
                orderservice.push({
                  "EmployeeName": {
                    "EMail": myservice.EmployeeName.EMail,
                    "FirstName": myservice.EmployeeName.FirstName,
                    "LastName": myservice.EmployeeName.LastName,
                    "JobTitle": myservice.EmployeeName.JobTitle
                  },
                  "MediaHouse": myservice.MediaHouse,
                  "ID":myservice.ID
                });
              });
              res.data.value.forEach(otherservice => {
                orderservice.push({
                  "EmployeeName": {
                    "EMail": otherservice.EmployeeName.EMail,
                    "FirstName": otherservice.EmployeeName.FirstName,
                    "LastName": otherservice.EmployeeName.LastName,
                    "JobTitle": otherservice.EmployeeName.JobTitle
                  },
                  "MediaHouse": otherservice.MediaHouse,
                  "ID": otherservice.ID
                });
              });
              var item; item = orderservice;
              this.setState({ items: item, isLoading: false });
            }
          }
          else { //If in data Filterd no one is your serviceline
            this.setState({ items: res.data.value, isLoading: false });
          }
        }
        else { this.setState({ items: res.data.value, isLoading: false }); }
      }
      else {
        this.setState({
          isLoading: false, items: [
            {
              EmployeeName: {
                EMail: '',
                FirstName: '',
                LastName: '',
                JobTitle: ''
              },
              MediaHouse: 'No SpotLights to Display',
              ID:0
            }
          ]
        });
      }

    }).catch(error => {
      console.log(error);
    });
  }

  public render(): React.ReactElement<ISpotlightProps> {
    var queryParameters = new UrlQueryParameterCollection(window.location.href);
    if (parseInt(queryParameters.getValue("SpotlightID")) > 0) {
      return (<SpotlightID graphClient={this.props.graphClient}
        siteUrl={this.props.siteUrl}
        listId={this.props.listId}
        serviceline={this.props.serviceline}
        numberOfSeconds={this.props.numberOfSeconds}
        Homepage={this.props.Homepage}
      />);
    } else {
      //property of Slider
      var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: Number(this.props.numberOfSeconds + '000'),
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />
      };
      return (
        <div className={styles.spotlight}>

          {(this.props.Homepage == true) ? (
            <div className={styles.container}>
              <h2>SPOTLIGHTS</h2>
              {
                // If data is null then will show message of "No SpotLights to Display"
                (this.state.items[0].MediaHouse === "No SpotLights to Display")
                  ?
                  <div className={`ms-Grid ${styles.padnone}`} dir="ltr">
                    <div className="ms-Grid-row">
                      <div className="ms-Grid-col ms-sm3 ms-md3">
                        <img src={`${this.props.siteUrl}/_layouts/15/userphoto.aspx?size=L`} style={{ width: '100px' }} />
                      </div>
                      <div className="ms-Grid-col ms-sm9 ms-md9">
                        <div className={styles.spotdetails}>
                          <h2>No SpotLights to Display</h2>
                        </div>
                      </div>
                    </div>
                  </div>
                  :
                  <div className={`ms-Grid ${styles.padnone}`} dir="ltr">
                    <Slider {...settings}>
                      {this.state.items.map((item, key) => {
                        return (<div className="ms-Grid-row">
                          <div className="ms-Grid-col ms-sm3 ms-md3">
                            <img src={`${this.props.siteUrl}/_layouts/15/userphoto.aspx?size=L&accountname=${item.EmployeeName.EMail}`} style={{ width: '100px' }} />

                          </div>
                          <div className="ms-Grid-col ms-sm9 ms-md9">
                            <div className={styles.spotdetails}>
                              <h2> {item.EmployeeName.FirstName} {item.EmployeeName.LastName}</h2>
                              <p> {item.EmployeeName.JobTitle}</p>
                              <p>{(item.MediaHouse).substring(0, 150)}</p>
                              <p> <a href={`${this.props.siteUrl}/SitePages/Spotlights.aspx?SpotlightID=${item.ID}`} className={styles.readmore} target="_blank">Read More</a></p>
                            </div>
                          </div>
                        </div>);
                      })}
                    </Slider>
                  </div>
              }
              <Link href={`${this.props.siteUrl}/SitePages/Spotlight.aspx`} target='_blank' className={styles.viewAll}>
                view more
          </Link>
            </div>) : (
              <AllSpotlight siteUrl={this.props.siteUrl} listId={this.props.listId}
                numberOfSeconds={this.props.numberOfSeconds}
                Homepage={this.props.Homepage}
                serviceline={this.props.serviceline}
                graphClient={this.props.graphClient} />)}
        </div>
      );
    }
  }
}

function SampleNextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{ ...style, display: "block", background: "black", right: "0", top: "10px" }}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{
        ...style, display: "block", background: "black", left: "92%", top: "10px", position: "absolute", zIndex: "1"
      }}
      onClick={onClick}
    />
  );
}
